import uniPopup from "@/components/uni-popup/uni-popup.vue";
import {
	mapState,
	mapMutations
} from 'vuex';
import {
	authTouTiaoSign,
	authWechatSign,
	groupJoinGroup,
	groupListGroup,
	groupQuitGroup,
	groupSetDefault,
	getAuthCode,
	getBindPhone
} from '../../../service/service.js';
import {
	ActionJcdl
} from '@/utils/actions.js';
import store from '@/store/index.js';

export default {
	components: {
		uniPopup
	},

	data() {
		let deftReceiptItems = [{
			fymc: '无',
			fyId: 0,
			invoices: [],
			zs: 0,
			je: 0
		}];
		let deftRimbsTags = [{
				id: 1,
				entryName: "交通费"
			},
			{
				id: 2,
				entryName: "住宿费"
			},
			{
				id: 3,
				entryName: "餐饮费"
			},
			{
				id: 4,
				entryName: "快递费"
			},
			{
				id: 5,
				entryName: "办公费"
			},
			{
				id: 6,
				entryName: "通讯费"
			},
			{
				id: 7,
				entryName: "其他"
			},
		];
		return {
			group: {
				name: "无"
			},
			isEmpty: true, //是否有组织
			isBind: false, //是否绑定手机
			tel: '',
			code: '', //验证码
			receiptItems: deftReceiptItems,
			rimbsTags: deftRimbsTags,
			edit: true
		};
	},
	computed: {
		...mapState(['hasLogin', 'userInfo']),
		groups: function() {
			return store.state.mine.addedGroups;
		},
		message: function() {
			return this.isBind ? this.tel : '未绑定';
		},
	},
	onLoad(option) {},
	onShow() {
		if (store.state.hasLogin) {
			this.loadListGroup();
		}
		if (store.state.mine.popupScan) {
			console.log('弹出扫码加入企业..')
			this.$refs.gList.open();
			store.state.mine.popupScan = false;
		}
		// this.handleChargeChange()
	},
	watch: {
		'groups': function(val) {
			store.state.hasGroup = val.length > 0;
		}
	},
	methods: {
		...mapMutations(['login']),
		// 选择企业
		handleChooseGroup() {
			this.$refs.gList.open();
		},
		// 关于我们
		handleAboutUs() {
			uni.navigateTo({
				url: '/pages/mine/about-us/about-us_tt'
			});
		},
		//头条授权登录 
		handleTouTiaoLogin() {
			tt.login({
				success: (res) => {
					console.log('登录成功..', res);
					if (res.errMsg !== 'login:ok') {
						uni.showModal({
							title: '',
							content: '请求登录授权失败：' + res.errMsg,
							showCancel: false
						});
						return;
					}
					authTouTiaoSign({
						code: res.code
					}).then((awRes) => {
						console.log('授权成功..', awRes);
						if (awRes.success) {
							let userInfo = awRes.data;
							// user.userId = awRes.data.userId;
							this.login(userInfo);
							store.state.hasLogin = true;
							uni.showToast({
								title: '登录成功',
								icon: 'none',
								duration: 2000
							});
							uni.$emit('LOGIN_SUCCESS', {});
							// console.log('登录用户信息..', store.state.userInfo);
							// this.loadListGroup();
						}
					});

				},
				fail: (err) => {
					console.log('授权失败..', err);
					uni.showModal({
						title: '',
						content: '授权失败：' + err,
						showCancel: false
					});
				}
			});
		},
		//微信授权登录
		handleWeChatLogin(detail) {
			uni.login({
				provider: 'weixin',
				success: (res) => {
					console.log('登录成功..', res);
					if (res.errMsg !== 'login:ok') {
						uni.showModal({
							title: '',
							content: '请求登录授权失败：' + res.errMsg,
							showCancel: false
						});
						return;
					}
					authWechatSign({
						code: res.code
					}).then((awRes) => {
						console.log('授权成功..', awRes);
						if (awRes && awRes.success) {
							let user = detail.userInfo;
							user.userId = awRes.data.userId;
							this.login(user);
							uni.$emit('LOGIN_SUCCESS', {});
							console.log('登录用户信息..', store.state.userInfo);
							this.loadListGroup();
						}
					});
				},
				fail: (err) => {
					console.log('授权失败..', err);
					uni.showModal({
						title: '',
						content: '授权失败：' + err,
						showCancel: false
					});
				}
			});
		},
		// 授权登录
		handleLogin(rsp) {
			console.log('点击了登录..');
			let detail = rsp.detail;
			let errMsg = detail.errMsg;
			if (errMsg !== 'getUserInfo:ok') {
				uni.showModal({
					title: '',
					content: '获取用户信息失败：' + errMsg,
					showCancel: false
				});
				return;
			}




			this.handleWeChatLogin(detail);



		},
		// 企业列表显隐
		handleGListPopupChange(e) {
			let args = {
				animation: true
			};
			if (e.show) {
				uni.hideTabBar(args);
			} else {
				uni.showTabBar(args);
			}
		},
		// 关闭企业列表
		handelCloseGList() {
			this.$refs.gList.close();
		},
		// 扫码加入企业
		handleScan() {
			let that = this;
			uni.scanCode({
				onlyFromCamera: true,
				scanType: 'qrCode',
				success: function(res) {
					//企业码格式：TenantId,企业ID,企业名称
					let cont = res.result;
					console.log('扫码识别的内容..', res);
					that.joinToGroup(cont);
				},
				fail: function(err) {
					console.log('扫码取消/失败!!', err);
				}
			});
			/* 测试代码 */
			// let cont = '8,35,多啦财税科技有限公司';
			// let cont = '3,36,河北悟诚信息技术有限公司';
			// console.log('扫码识别的内容：', cont);
			// that.joinToGroup(cont);
		},
		// 退出已加企业
		handleQuitGroup(e) {
			let that = this;
			let gid = e.target.id;
			console.log('请求退出已加企业..', gid);
			groupQuitGroup({
				userGroupId: gid
			}).then((res) => {
				console.log('退出已加企业结果..', res);
				if (res && res.success) {
					let ggs = store.state.mine.addedGroups;
					for (let i = 0; i < ggs.length; i++) {
						if (ggs[i].id == gid) {
							ggs.splice(i, 1);
							break;
						}
					}
				}
			});
		},
		// 设置默认企业
		handleDefaultGroupChange(e) {
			let that = this;
			let gid = e.detail.value;
			console.log('请求设置默认企业..');
			groupSetDefault({
				userGroupId: gid
			}).then((res) => {
				console.log('设置默认企业结果..', res);
				if (res && res.success) {
					let ggs = store.state.mine.addedGroups;
					for (let i = 0; i < ggs.length; i++) {
						let g = ggs[i];
						if (g.id == gid) {
							g.default = true;
						} else {
							if (g.default) {
								g.default = false;
							}
						}
						if (g.default) {
							that.group = g;
						}
					}
				}
			});
		},
		// 加载企业列表
		loadListGroup() {
			let that = this;
			let ggs = store.state.mine.addedGroups;
			if (ggs.length < 1) {
				console.log('请求获取企业列表..');
				groupListGroup().then((res) => {
					console.log('获取企业列表结果..', res);
					if (res && res.success) {
						let gs = res.data;
						ggs = gs;
						setDeftGroup(gs);
					}
				});
			} else {
				setDeftGroup(ggs);
			}

			function setDeftGroup(gs) {
				that.isEmpty = (gs.length < 1);
				if (gs.length > 0) {
					for (let i = 0; i < gs.length; i++) {
						if (gs[i].default) {
							that.group = gs[i];
							break;
						}
					}
				}
			}
		},
		joinToGroup(cont) {
			let that = this;
			if (!(/^\d{1,5},\d{1,5},[^,]+$/.test(cont))) {
				uni.showModal({
					title: '',
					content: '请扫描多啦票单通企业二维码',
					showCancel: false,
					confirmText: '我知道了'
				});
				return;
			}
			let props = cont.split(',');
			let tenantId = props[0];
			let groupId = props[1];
			let groupName = props[2];
			uni.showModal({
				title: '温馨提示',
				content: `您确认加入【${groupName}】吗？`,
				success: function(result) {
					if (result.confirm) {
						console.log('请求加入到企业..');
						groupJoinGroup({
							tenantId: tenantId,
							groupId: groupId
						}).then((jgRes) => {
							console.log('加入到企业结果..', jgRes);
							if (jgRes && jgRes.success) {
								let g = jgRes.data;
								let ggs = store.state.mine.addedGroups;
								ggs.push(g);
								if (that.isEmpty) {
									that.isEmpty = false;
								}
								if (g.default) {
									that.group = g;
								}
							}
						});
					} else if (result.cancel) {
						console.log('取消加入到企业..');
					}
				},
				cancel: function() {
					console.log('取消加入到企业..');
				}
			});
		},

		
		// 绑定手机弹层
		handleShowbindPhone() {
			let dl = ActionJcdl();
			if (!dl) {
				return;
			}
			this.$refs.bindPhone.open();
		},
		handleShowBindCode() {
			if (this.tel.length != 11) {
				uni.showToast({
					title: "手机号不正确",
					icon: 'none',
					duration: 2000
				});
				return;
			}
			getAuthCode({
				phone: this.tel
			}).then((res) => {
				if(!res.success) {
					return ;
				}
				console.log("已发送验证码", res);
				this.$refs.bindPhone.close();
				this.$refs.bindCode.open();
			})
			
		},
		handleShowIsBind() {
			getBindPhone({
				code: this.code,
				phone: this.tel
			}).then((res) => {
				console.log("绑定手机号", res)
				if (!res.success) {
					uni.showToast({
						title: res.message,
						icon: 'none',
						duration: 2000
					});
					return;
				}
				this.tel = this.tel;
				this.isBind = true;
				this.$refs.bindCode.close();
				this.$refs.bind.close();

			})

		},
		// 选择费用项目
		handleChargeChange(e) {
			console.log('选择更改项目费用..', e);
			let riidx = e.currentTarget.id;
			let rtidx = e.detail.value;
			let ri = this.receiptItems[riidx];
			let rt = this.rimbsTags[rtidx];
			if (rt) {
				if (ri.fyId == rt.id) {
					console.log('项目费用没有更改..');
					return;
				}
				ri.fyId = rt.id;
				ri.fymc = rt.entryName;
			}
		},
		// 加载报销标签
		loadRimbsTags(groupId) {
			let that = this;
			console.log('请求获取报销标签..');
			reimburseListRimbsTags(groupId).then((lrtRes) => {
				console.log('获取报销标签结果..', lrtRes);
				if (lrtRes && lrtRes.success) {
					that.rimbsTags = lrtRes.data;
				}
			});
		},
	}
}
